/*
	author : zhupinglei,
	desc : 浏览器重置
*/

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-family: -apple-system, SF UI Text, Helvetica Neue, Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  color: #000;
  line-height: 1.4;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
      text-size-adjust: 100%;
  background: #fff;
  -webkit-user-select: none;
  /* 禁止选中文本（如无文本选中需求，此为必选项） */
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

body div,
body dl,
body dt,
body dd,
body ul,
body ol,
body li,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6,
body pre,
body form,
body fieldset,
body input,
body p,
body a,
body blockquote,
body th,
body i,
body u,
body span {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

body ol,
body ul {
  list-style: none;
}

body u,
body i {
  font-weight: normal;
  font-style: normal;
}

body a {
  color: #000;
  text-decoration: none;
}

body a,
body img {
  -webkit-touch-callout: none;
  /* 禁止长按链接与图片弹出菜单 */
}

body input,
body select,
body button,
body textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  -o-appearance: none;
  border: none;
  background-color: #fff;
  margin: 0;
  padding: 0;
  resize: none;
  outline: none;
  border-radius: 0;
}

body table {
  border-collapse: collapse;
  border-spacing: 0;
}

body .clearfix {
  *zoom: 1;
}

body .clearfix:after {
  display: block;
  overflow: hidden;
  clear: both;
  height: 0;
  visibility: hidden;
  content: ".";
}

.muivue-page-out-enter-active,
.muivue-page-out-leave-active,
.muivue-page-in-enter-active,
.muivue-page-in-leave-active {
  will-change: transform;
  height: 100%;
  width: 100%;
  position: absolute;
}

.muivue-page-out-enter-active {
  left: 100%;
  -webkit-animation: pageInLeft 0.5s ease-out forwards;
          animation: pageInLeft 0.5s ease-out forwards;
}

.muivue-page-out-leave-active {
  -webkit-animation: pageOutRight 0.5s ease-out forwards;
          animation: pageOutRight 0.5s ease-out forwards;
}

.muivue-page-in-enter-active {
  left: 100%;
  -webkit-animation: pageInRight 0.5s ease-in forwards;
          animation: pageInRight 0.5s ease-in forwards;
}

.muivue-page-in-leave-active {
  -webkit-animation: pageOutLeft 0.5s ease-in forwards;
          animation: pageOutLeft 0.5s ease-in forwards;
}

@-webkit-keyframes pageInLeft {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes pageInLeft {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@-webkit-keyframes pageOutLeft {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes pageOutLeft {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@-webkit-keyframes pageInRight {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes pageInRight {
  0 {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@-webkit-keyframes pageOutRight {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

@keyframes pageOutRight {
  0 {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
  }
}

.index ul {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.index ul li {
  -webkit-box-flex: 1;
              -ms-flex: 1;
          flex: 1;
  border: 0.013333rem solid #eee;
}

[data-dpr="1"] .index ul li {
  height: 50px;
  font-size: 12.5px;
  line-height: 50px;
}

[data-dpr="2"] .index ul li {
  height: 100px;
  font-size: 25px;
  line-height: 100px;
}

[data-dpr="3"] .index ul li {
  height: 150px;
  font-size: 37.5px;
  line-height: 150px;
}

.index ul li a {
  width: 100%;
  display: block;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}


